import React                            from 'react';
import {Route, Switch} from 'react-router-dom';
import Container                        from './Container';
import Menu                             from './menu/Menu';
import MenuLink                         from './menu/MenuLink';
import Home                             from './Home';
import Login                            from './Login';
import DesktopApplication               from './DesktopApplication';
import Employees                        from './employees/Employees';
import Footer                           from './Footer';

class Company extends React.Component {

    render() {
        return (
             <div className="app">
                 <Menu>
                     <MenuLink path={this.props.match.url+'/home'} title="Home" glyphicon="fa fa-home"/>
                     {/*
                     <MenuLink path={this.props.match.url+'/login'} title="Login" glyphicon="fa fa-sign-in "/>
                     <MenuLink path={this.props.match.url+'/salaries'} title="salaries" glyphicon="fa fa-users" />
                     */}
                     <MenuLink path={this.props.match.url+'/application'} title="application" glyphicon="fa fa-tachometer"/>
                 </Menu>

                 <Container>
                     <Switch>
                      {/*
                      <Route exact path="/login" render={ (props) => (<Login {...props} onLogin={this.onLogin} />)} />
                      <Route exact path={this.props.match.url+'/login'} component={ (props) => (<Login {...props} notify={this.props.notify} onLogin={this.props.onLogin} />)} />
                      <Route exact path={this.props.match.url+'/salaries'} component={ () => (<Employees notify={this.props.notify} />)} />
                      */}
                      <Route exact path={this.props.match.url+''} component={ () => (<Home notify={this.props.notify} />)} />
                      <Route exact path={this.props.match.url+'/home'} component={ () => (<Home notify={this.props.notify} />)} />

                      <Route exact path={this.props.match.url+'/application'} component={ () => (<DesktopApplication notify={this.props.notify} />)} />
                     </Switch>
                  </Container>
                  <Footer/>
            </div>
        );
    }
}

export default Company;



