import React from 'react';
import {withRouter} from 'react-router-dom';
import avatar from '../images/avatar.png';

class Login extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: ''
        }
        this.handleUsernameChange = this.handleUsernameChange.bind(this);
        this.handlePasswordChange = this.handlePasswordChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleUsernameChange(event){
        this.setState({username: event.target.value});
	}

    handlePasswordChange(event){
        this.setState({password: event.target.value});
	}

    handleSubmit(event){
        event.preventDefault();

        // prepare data for login API
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        let form = new FormData(document.getElementById('login-form'));

        // Send email/password to login API and get response
        fetch('/api/public/login', {
            method: 'POST',
            headers: myHeaders,
            body: form
        })
            .then(function(response) {
                return response.json();
            })
            .then((jsonData)=> {
                // if login success and token given
                if(jsonData.status === 401){
                    // failed to login
                    this.props.notify("Erreur d'authentification",'error');
                    console.log(" Failed to login, Error message : "+ jsonData.message);
                }else{
                    console.log(" ----- login -----");
                    console.log(" ----- jsonData -----", jsonData);
                    this.props.notify("Utilisateur connecté ",'info');
                    // save token and username in session
                    this.props.onLogin(jsonData.company.interlocutor_name,jsonData.token);

                    // redirect to dashboard
                    this.props.history.push('/en/home');
                }
            } )
            .catch(err => {
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
                this.props.notify("Service en panne",'error');
                console.log("Service down, please wait for re-submit, Error message : " + err);
            });
	}


	render(){
		return (
			<div className="login-card"><img src={avatar} alt="./images/avatar.png" className="profile-img-card" />
				<p className="profile-name-card"> </p>
				<form  onSubmit={this.handleSubmit} id="login-form" className="form-signin">
					<input name="username" value={this.state.username} onChange={this.handleUsernameChange} className="form-control" type="text" placeholder="Username" autoFocus="" id="inputEmail" required />
					<input name="password" value={this.state.password} onChange={this.handlePasswordChange} className="form-control" type="password" placeholder="Password" id="inputPassword" required />
                    <button type="submit" className="btn btn-lg btn-default">Login</button>
				</form>
			</div>
		)
	}
}

export default withRouter(Login);


