
//export var DoctorsPromise = new Promise(getDoctorsList);

export function getDoctorsList(resolve, reject){

    // prepare data for get profile API
    let myHeaders = new Headers();
    myHeaders.append("Accept", "application/json");
    myHeaders.append("x-access-token", sessionStorage.getItem('token'));

    // call get profile API and get response
    fetch(`/api/doctor/list`, {
        method: 'GET',
        headers: myHeaders,
    })
        .then(function(response) {
            return response.json();
        })
        .then((doctors)=> {
            console.log('>>>> API list of doctors THEN', doctors);
            resolve(doctors);
            //this.setState({doctors: jsonData});
            //callback(true);  
        })
        .catch( error => {
            // can't get response (server is down, ....) ==> show error message to user (unable ...)
            console.log('>>>>  list of doctors CATCH', `can't get response (server is down, ....)`);
            reject(error)
            //callback(false,'error',"Service down, please wait for re-submit ==> " + err);
        });
}

export function addDoctor(form){
    // prepare data for edit profile API
    return function (resolve, reject){
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("x-access-token", sessionStorage.getItem('token'));


        //let form = new FormData(document.getElementById('add_doctor_form'));
        // Send form to submit app API and get response
        fetch('/api/doctor/new', {
            method: 'POST',
            headers: myHeaders,
            body: form,
        })
            .then(function (response) {
                return response.json();
            })
            .then((jsonData) => {
                console.log("/api/doctor/new' POST then", jsonData);
                resolve(jsonData);
            })
            .catch(error => {
                console.log("/api/doctor/new' POST catch", error);
                reject(error)
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
            });
        }
}


