import React                            from 'react';
import {BrowserRouter as Router ,Route, Switch} from 'react-router-dom';
import Header                           from './components/Header';
import Admin                            from './components/Admin';
import Company                          from './components/Company';
import Doctor                           from './components/Doctor';
import Login                           from './components/Login';
import Footer                           from './components/Footer';

import './App.css';
import { ToastContainer, toast } from 'react-toastify';

class App extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            connected: false,
            user: null,
            token: ""
        };
        this.onLogin = this.onLogin.bind(this);
        this.onLogout = this.onLogout.bind(this);
        this.notify = this.notify.bind(this);
    }

    onLogin(user,token){
        console.log("*************  onLogin  **************");
        this.setState({
            user: user,
            token: token,
            connected: true
        });
        sessionStorage.setItem('token', this.state.token);
        sessionStorage.setItem('user', JSON.stringify(this.state.user));
    }

    onLogout(){
        console.log("*************  onLogout  **************");
        this.setState({
            user: null,
            token: "",
            connected: false
        });
        sessionStorage.removeItem('user');
        sessionStorage.removeItem('token');
    }

    notify(msg,type){
        let msgPosition = {position: toast.POSITION.TOP_RIGHT}
        switch(type) {
            case 'success':
                toast.success(msg,msgPosition); break;
            case 'error':
                toast.error(msg,msgPosition); break;
            case 'warn':
                toast.warn(msg,msgPosition); break;
            case 'info':
                toast.info(msg,msgPosition); break;
            default : toast(msg,msgPosition);
        }
    };

    render() {
        return (
             <Router>
                 <div>
                     <Header/>
                     <ToastContainer autoClose={5000} hideProgressBar={true} />
                     <Switch>
                         <Route exact path="/" component={ (props) => (<Login {...props} notify={this.notify} onLogin={this.onLogin} />)} />

                         <Route path="/ad" component={ (props)=> (<Admin {...props} notify={this.notify} onLogin={this.onLogin} />)}/>
                         <Route path="/en" component={ (props)=> (<Company {...props} notify={this.notify} onLogin={this.onLogin} />)}/>
                         <Route path="/dr" component={ (props)=> (<Doctor {...props} notify={this.notify} onLogin={this.onLogin} />)}/>
                     </Switch>
                     <Footer/>
                </div>
            </Router>
        );
    }
}

export default App;



