import React from 'react';
const Datastore = require('nedb');
//const path      = require('path');

let nedb = new Datastore({ filename:"C:/Users/IT-CAN/Documents/syndicat-front-end-app/src/components/employees/rtiUser.db", autoload:true });

class AddEmployee extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            employee:{
                lastname: '',
                firstname: '',
                cin: '',
                registration_number: '',
                city: '',
                chain: '',
                birth_date: '',
                sex: '',
                status: '',
                children_number: '',
                hire_date: '',
                address: '',
                responsible: '',
            },
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(fieldName) {
        return ((event) => {
            let employee = this.state.employee;
            employee[fieldName] = event.target.value;
            this.setState({employee: employee});
        } );
    }

    handleSubmit(event) {
        console.log('A employee was submitted: ' + JSON.stringify(this.state.employee));
        event.preventDefault();

        // prepare data for edit profile API
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("x-access-token", sessionStorage.getItem('token'));

        let form = new FormData(document.getElementById('add_employee_form'));
        // Send form to submit app API and get response
        fetch('/api/employee/add', {
            method: 'POST',
            headers: myHeaders,
            body: form,
        })
            .then(function (response) {
                return response.json();
            })
            .then((jsonData) => {
                console.log("/api/employee/new' POST then", jsonData);
                this.props.refreshEmployeeList();
            })
            .catch(err => {
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
                console.log("/api/employee/new' POST catch", err);
            });
    }

    handleSubmitTest(event) {

        event.preventDefault();

        nedb.loadDatabase(function(err) {
            if (err) return alert("error ==>" + err);

            let newEmployee = {
                lastname : "rti",
                firstname : "Turki",
                cin : "1233456",
                registration_number : "147852" ,
            };

            nedb.insert(newEmployee, function (err, new_employee) {
                alert(' new employee ==> '+ nedb.filename);
                console.log(' new employee ==> ', new_employee);

            });

        });

    }

    render(){
        return (
            <div className="col-sm-6">
                <div className="card">
                    <div className="card-body">
                        <button onClick={this.props.hideAddEmployeeForm} type="button" className="btn btn-default btn-circle btn-remove"><i className="fa fa-remove"/></button>
                        <h5 className="card-title">Ajouter un salarié</h5>
                        <form id="add_employee_form"  onSubmit={this.handleSubmitTest}>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Nom</label>
                                <div className="col-sm-8">
                                    <input name="lastname" type="text" className="form-control" value={this.state.employee.lastname} onChange={this.handleChange('lastname')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Prenom</label>
                                <div className="col-sm-8">
                                    <input name="firstname" type="text" className="form-control" value={this.state.employee.firstname} onChange={this.handleChange('firstname')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">CIN</label>
                                <div className="col-sm-8">
                                    <input name="cin" type="number" className="form-control" value={this.state.employee.cin} onChange={this.handleChange('cin')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Matricule</label>
                                <div className="col-sm-8">
                                    <input name="registration_number" type="number" className="form-control" value={this.state.employee.registration_number} onChange={this.handleChange('registration_number')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Ville</label>
                                <div className="col-sm-8">
                                    <input name="city" type="text" className="form-control" value={this.state.employee.city} onChange={this.handleChange('city')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Chaine</label>
                                <div className="col-sm-8">
                                    <input name="city" type="text" className="form-control" value={this.state.employee.chain} onChange={this.handleChange('chain')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Date de naissance</label>
                                <div className="col-sm-8">
                                    <input name="birth_date" type="date" className="form-control" value={this.state.employee.birth_date} onChange={this.handleChange('birth_date')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Sexe</label>
                                <div className="col-sm-8">
                                    <input name="sex" type="text" className="form-control" value={this.state.employee.sex} onChange={this.handleChange('sex')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label" htmlFor="statut">Statut</label>

                                <div className="col-sm-8">
                                    <select name="status" className="custom-select" id="statut" value={this.state.employee.status} onChange={this.handleChange('status')}>
                                        <option value="0">Choose...</option>
                                        <option value="1">One</option>
                                        <option value="2">Two</option>
                                        <option value="3">Three</option>
                                    </select>
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Nombres enfants</label>
                                <div className="col-sm-8">
                                    <input name="children_number" type="number" className="form-control" value={this.state.employee.children_number} onChange={this.handleChange('children_number')} />
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Date embauche</label>
                                <div className="col-sm-8">
                                    <input name="hire_date" type="date" className="form-control" value={this.state.employee.hire_date} onChange={this.handleChange('hire_date')} />
                                </div>
                            </div>

                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Adresse</label>
                                <div className="col-sm-8">
                                    <input name="address" type="text" className="form-control" value={this.state.employee.address} onChange={this.handleChange('address')} />
                                </div>
                            </div>
                            <fieldset className="form-group">
                                <div className="row">
                                    <legend className="col-form-label col-sm-4 pt-0">Responsable</legend>
                                    <div className="col-sm-8">
                                        <div className="form-check-inline">
                                            <input name="responsible" className="form-check-input" type="radio"  value="option1"  />
                                            <label className="form-check-label" htmlFor="gridRadios1">Yes</label>
                                        </div>
                                        <div className="form-check-inline">
                                            <input name="responsible" className="form-check-input" type="radio"  value="option2" />
                                            <label className="form-check-label" htmlFor="gridRadios2">No</label>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>
                            <div className="form-group row">
                                <div className="col-sm-12">
                                    <button type="submit" className="btn btn-primary">Appliquer</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default AddEmployee;




