import React from 'react';
import {NavLink} from 'react-router-dom';

class MenuLink extends React.Component {

    render(){
        return (
            <NavLink isActive={(match, location)=>{ if (!match) return false; return (match.path === location.pathname)}} style={{textDecoration:"none"}} to={this.props.path}>
                <button type="button" className={"list-group-item btn btn-light btn-block text-uppercase menu-link "+this.props.isselected}>
                    <i className={"glyphicon-style "+this.props.glyphicon} aria-hidden="true"/>
                    {this.props.title}
                </button>
            </NavLink>
        )
    }
}

export default MenuLink;


