import React from 'react';

class Container extends React.Component {

	render(){
	    return (
            <div className="app-container ">
                {/* onClick={this.props.notify.bind(this,'rti is the best ;)' ,'error')} */}
                {this.props.children}
            </div>
        )
    }
}

export default Container;


