import React from 'react';
import {withRouter} from 'react-router-dom';

class Certificate extends React.Component {
	render(){
		return (
            <div className="card">
                <div className="card-body">
                    <h5 className="card-title ">Certificat de repos</h5>

                    <form className="row">
                        <div className="col-md-6">
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label" htmlFor="docteurs">Docteur</label>

                                <div className="col-sm-8">
                                    <select className="custom-select" id="docteurs" defaultValue="0">
                                        <option value="1">Choose...</option>
                                        <option value="1">One</option>
                                        <option value="2">Two</option>
                                        <option value="3">Three</option>
                                    </select>
                                </div>
                            </div>
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Prenom</label>
                                <div className="col-sm-8">
                                    <input type="text" className="form-control" />
                                </div>
                            </div>

                            <fieldset className="form-group">
                                <div className="row">
                                    <legend className="col-form-label col-sm-4 pt-0">Autorisation de repos</legend>
                                    <div className="col-sm-8">
                                        <div className="form-check-inline">
                                            <input className="form-check-input" type="checkbox"/>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>

                            <fieldset className="form-group">
                                <div className="row">
                                    <legend className="col-form-label col-sm-4 pt-0">Ordonnance</legend>
                                    <div className="col-sm-8">
                                        <div className="form-check-inline">
                                            <input className="form-check-input" type="checkbox"/>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>

                            <fieldset className="form-group">
                                <div className="row">
                                    <legend className="col-form-label col-sm-4 pt-0">Facture</legend>
                                    <div className="col-sm-8">
                                        <div className="form-check-inline">
                                            <input className="form-check-input" type="checkbox"/>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>

                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Nombre de jours</label>
                                <div className="col-sm-8">
                                    <input type="number" className="form-control" />
                                </div>
                            </div>

                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Statut</label>
                                <div className="col-sm-8">
                                    <input type="text" className="form-control" />
                                </div>
                            </div>

                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Raison de l'arret </label>
                                <div className="col-sm-8">
                                    <textarea className="form-control" />
                                </div>
                            </div>

                        </div>
                        <div className="col-md-6">
                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Accident de travail</label>
                                <div className="col-sm-8">
                                    <input type="text" className="form-control" />
                                </div>
                            </div>

                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Accident de trajet</label>
                                <div className="col-sm-8">
                                    <input type="text" className="form-control" />
                                </div>
                            </div>

                            <fieldset className="form-group">
                                <div className="row">
                                    <legend className="col-form-label col-sm-4 pt-0">Maladie Prof</legend>
                                    <div className="col-sm-8">
                                        <div className="form-check-inline">
                                            <input className="form-check-input" type="checkbox"/>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>

                            <fieldset className="form-group">
                                <div className="row">
                                    <legend className="col-form-label col-sm-4 pt-0">Rechute accident de travail</legend>
                                    <div className="col-sm-8">
                                        <div className="form-check-inline">
                                            <input className="form-check-input" type="checkbox"/>
                                        </div>
                                    </div>
                                </div>
                            </fieldset>


                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label">Observations </label>
                                <div className="col-sm-8">
                                    <textarea className="form-control" />
                                </div>
                            </div>

                            <div className="form-group row">
                                <label className="col-sm-4 col-form-label" htmlFor="type">Type</label>

                                <div className="col-sm-8">
                                    <select className="custom-select" id="type" defaultValue="0">
                                        <option value="0">Choose...</option>
                                        <option value="1">One</option>
                                        <option value="2">Two</option>
                                        <option value="3">Three</option>
                                    </select>
                                </div>
                            </div>

                            <div className="form-group row">
                                <div className="col-sm-12">
                                    <button type="submit" className="btn btn-primary">Appliquer</button>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
		)
	}
}

export default withRouter(Certificate);


