import React from 'react';
import {withRouter} from 'react-router-dom';
import AddEmployee from './AddEmployee';
import EmployeeRow from './EmployeeRow';
import Loader from '../common/Loader';
import Error from '../common/Error';

class Employees extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            employees: [],
            status: "success",
            showAddEmployeeForm: false,
        };
        this.getEmployeesList = this.getEmployeesList.bind(this);
        this.callApiGetEmployeesList = this.callApiGetEmployeesList.bind(this);
    }

    componentDidMount(){
        //this.getEmployeesList();
    }

    getEmployeesList(){
        this.callApiGetEmployeesList((success,type,msg)=>{
            if (!success) {
                this.props.notify( msg ,type);
                this.setState({
                    status: "error",
                });

            }else{
                this.setState({
                    status: "success"
                });
            }
        });
    }

    callApiGetEmployeesList(callback){

        // prepare data for get profile API
        let myHeaders = new Headers();
        myHeaders.append("Accept", "application/json");
        myHeaders.append("x-access-token", sessionStorage.getItem('token'));
/*
        let username = sessionStorage.getItem('user') ? JSON.parse(sessionStorage.getItem('user') ).username : null ;
        if (!username){
            callback(false,'error',"No user connected !!");
            return;
        }
*/
        // call get profile API and get response
        fetch(`/api/employee/list`, {
            method: 'GET',
            headers: myHeaders,
        })
            .then(function(response) {
                return response.json();
            })
            .then((jsonData)=> {
                this.setState({employees: jsonData});
                callback(true);
                console.log('>>>>  list of employees THEN', jsonData);
            })
            .catch( err => {
                // can't get response (server is down, ....) ==> show error message to user (unable ...)
                callback(false,'error',"Service down, please wait for re-submit ==> " + err);
            });

    }

	render(){

        switch(this.state.status) {
            case 'loading':
                return ( <Loader/>);
            case 'error':
                return ( <Error/>);
            default :
                let content = this.state.showAddEmployeeForm ? <AddEmployee refreshEmployeeList={this.getEmployeesList} hideAddEmployeeForm={()=>{this.setState({showAddEmployeeForm: false})}} /> : null;

                let employees = this.state.employees.map((employee, index)=> {
                    return (
                        <EmployeeRow key={index} employee={employee}/>
                    )
                });
                return (
                    <div className="row">
                        <div className="col-sm-6">
                            <div className="card">
                                <div className="card-body">
                                    <h5 className="card-title">
                                        Salariés
                                        <button onClick={()=>{this.setState({showAddEmployeeForm: true})}} type="button" className="btn btn-default btn-circle"><i className="fa fa-plus"/></button>
                                    </h5>
                                    <table className="table table-striped table-bordered">
                                        <thead>
                                        <tr>
                                            <th scope="col">Nom</th>
                                            <th scope="col">CIN</th>
                                            <th scope="col">Matricule</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {employees}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        {content}
                    </div>
                )
        }
	}
}

export default withRouter(Employees);


