import React                            from 'react';
import {Route, Switch} from 'react-router-dom';
import Container                        from './Container';
import Menu                             from './menu/Menu';
import MenuLink                         from './menu/MenuLink';
import Home                             from './Home';
import Login                            from './Login';
import Companies                        from './companies/Companies';
//import Doctors                        from './doctors/Doctors';
//import DrModule from                     'doctors-module';
import {Doctors, DoctorsList, AddDoctor, Link} from 'doctors-module';
//import {DoctorsPromise}               from '../api';
import { getDoctorsList, addDoctor}   from '../api';
//import { getDoctorsList, addDoctor}     from 'doctors-web-api';
import Dashboard                        from './Dashboard';

class Admin extends React.Component {

    render() {
        return (

             <div className="app">
                 <Menu>
                     <MenuLink path={this.props.match.url+'/home'} title="Home" glyphicon="fa fa-home"/>
                     <MenuLink path={this.props.match.url+'/login'} title="Login" glyphicon="fa fa-sign-in "/>
                     <MenuLink path={this.props.match.url+'/entreprises'} title="entreprises" glyphicon="fa fa-building" />
                     <Link path={this.props.match.url+'/docteurs'}/>
                     <MenuLink path={this.props.match.url+'/tableau-de-bord'} title="tableau de bord" glyphicon="fa fa-tachometer"/>
                 </Menu>
                 <Container>
                     <Switch>
                      {/*<Route exact path="/login" render={ (props) => (<Login {...props} onLogin={this.onLogin} />)} />*/}
                      <Route exact path={this.props.match.url+''} component={ () => (<Home notify={this.props.notify} />)} />
                      <Route exact path={this.props.match.url+'/home'} component={ () => (<Home notify={this.props.notify} />)} />
                      <Route exact path={this.props.match.url+'/login'} component={ (props) => (<Login {...props} notify={this.props.notify} onLogin={this.props.onLogin} />)} />
                      <Route exact path={this.props.match.url+'/entreprises'} component={ () => (<Companies notify={this.props.notify} />)} />

                      <Route exact path={this.props.match.url+'/docteurs'} component={ ()=>
                        <Doctors getDoctorsList={getDoctorsList} addDoctor={addDoctor} notify={this.props.notify}>
                            <DoctorsList/>
                            <AddDoctor/>
                        </Doctors>
                       } />
                      
                      <Route exact path={this.props.match.url+'/tableau-de-bord'} component={ () => (<Dashboard notify={this.props.notify} />)} />
                     </Switch>
                  </Container>
            </div>
        );
    }
}

export default Admin;



