import React from 'react';
import {withRouter} from 'react-router-dom';
import window from './common/common';

class DesktopApplication extends React.Component {

    constructor(props){
        super(props);
        this.state = { }
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        this.props.notify('le fichier est importé avec succés ', 'info');
    }

	render(){
		return (
            <div className="card">
                <div className="card-body">
                    <h5 className="card-title ">Votre application de bureau </h5>

                    <div>
                        <h6>Version : 0.2</h6>
                        <a href={window.defaults.server+"/zip/gestion_des_absences.zip"} download> Télécharger l'application </a>
                        <br/>
                        <br/>
                        <h6>Version : 0.3</h6>
                        <a href={window.defaults.server+"/zip/Gestion-Des-Absences-win32-ia32.zip"} download> Windows 32 Bits </a>
                        <br/>
                        <a href={window.defaults.server+"/zip/Gestion-Des-Absences-win32-x64.zip"} download> Windows 64 Bits </a>
                        <br/>
                        <a href={window.defaults.server+"/zip/Gestion-Des-Absences-darwin-x64.zip"} download> Mac </a>
                        <br/>
                        <br/>    
                        <h6 className="card-title ">Importer le fichier de la base de données </h6>
                        <form className="form-inline" onSubmit={this.handleSubmit}>
                            <div className="form-group mb-2">
                                <input type="file" className="form-control" required />
                            </div>
                            <button type="submit" className="btn btn-primary mb-2">Importer</button>
                        </form>
                    </div>
                </div>
            </div>
		)
	}
}

export default withRouter(DesktopApplication);


